
html,
body {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}
body {
  background: url('http://img2.ph.126.net/cxUR62GEFprrqdnE17JF4Q==/1844505522485680538.jpg') fixed;
  background-repeat: no-repeat;
  background-size: 100% 200%
}
#app {
  display: grid;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  grid-template-columns: 0.25fr auto 0.25fr;
  grid-template-rows: 100px auto auto;
  grid-template-areas: '. on1 .' '. on2 .' '. on3 .';
}
#app h2 {
  color: #fff;
}
#app .adder {
  grid-area: on1;
}
#app .adder .submit {
  color: #fff;
  border: none;
  background: rgba(41,65,69,0.3);
}
#app .upperBox {
  grid-area: on2;
}
#app .underBox {
  grid-area: on3;
}
#app .event,
#app .eventDone {
  position: relative;
  width: 100%;
  text-align: left;
  font-size: 16px;
  padding-left: 0;
  line-height: 16px;
  background: #fff;
  padding: 10px;
}
#app .event .delBtn,
#app .eventDone .delBtn {
  background: #fff;
  border: none;
  position: absolute;
  right: 10px;
  cursor: pointer;
}
#app .eventDone {
  opacity: 0.6;
}